<script>
	import BookJc from '../components/BookJc'
	import BookRemove from '../components/BookRemove'
	export default{
		name: "Select",
		data(){
			return{
				imgArr:[
				'./img/banner1.png',
				'./img/banner2.png',
				'./img/banner3.png',
				],
				supportsIndex: 0,
				timer: null,
			}
		},
		methods: {
			nextPic(){
				this.supportsIndex++
				if(this.supportsIndex >= this.imgArr.length){
