<template>
<div>
	<div class="clearfix" v-for="(b, index) in dataUrl.slice(0,4)" :key="index">
		<router-link :to="/bookdeta/ + b._id">
		<img :src="[link + b.cover]" class="clear-left auto-img">
		<div class="clear-right">
			<h4 class="clear-name">
				<span>{{b.title}}</span>
				<i class="complete">完结</i>
			</h4>
			<p class="clear-desc">{{b.shortIntro}}</p>
			<h4 class="clear-cate">
				<span>{{b.minorCate}}</span> | <span class="c-red">{{b.latelyFollower}}</span>人气
			</h4>
		</div>
		</router-link>
	</div>
		
	<div class="move" >查看更多></div>
</div>	
	
</template>

<script>
	
	export default {
		name:"BookJc",
		data(){
			return{
				dataUrl:[],
				sex:'',
				uu:[],
				link: 'http://statics.zhuishushenqi.com'
			}
		},
		
		
		created(){
			this.uu = this.$store.state.sex
			this.axios.get(this.bookUrl).then(b => {
				this.dataUrl = b.data.books
				console.log(this.bookUrl)
			})
		},
		
		
		watch:{
			uu(){
				this.axios.get(this.bookUrl).then(b => {
					this.dataUrl = b.data.books
				})
			}
		},
		
		computed:{
			bookUrl(){
				
				let url = 'api/book/by-categories?gender=' + this.$store.state.sex + '&type=hot&major=同人&minor=&start=0&limit=3'
				return url
			}
		},
		
		
		
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	a{
		color: #000;
	}
	.clearfix{
		width: 100%;
		height: 5.6875rem;
		display: block;
		margin-bottom: 0.625rem;
		
	
	}
	.clear-left{
		width: 3.9375rem;
		height: 5rem;
		margin-left: 1.0625rem;
		float: left;
	}
	.clear-right{
		float: left;
		width:70%;
		height:5.6875rem;
		margin-left: 1.0625rem;
		border-bottom: 1px solid #ddd;
	}
	.clear-name{
		height: 1.375rem;
		line-height: 1.375rem;
		
		span{
			font-size: 0.875rem;
		}
		i{
			display: inline-block;
			border-radius: 0.125rem;
			margin-left: 0.4375rem;
			padding: 0.125rem;
			height: 0.825rem;
			background-color: green;
			line-height: 0.5rem;
			font-size: 10px;
			color: #fff;
			margin-bottom: 0.125rem;
			vertical-align:middle;
		}
	}
	.clear-desc{
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2; 
		-webkit-box-orient: vertical;
		color: #999;
		font-size: 0.8125rem;
		width: 100%;
		height: 1.6875rem;
		line-height: 14px;
		margin: 0.3125rem 0 0.5rem;
	}
	
	.clear-cate{
		font-size: 0.75rem;
		color: #666;
		font-weight: 400;
		
		span{
			margin-right: 0.1875rem;
		}
		
		.c-red{
			margin-left: 0.25rem;
			color: #b93321;
		}
	}
</style>
